import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import {
  Card,
  Container,
  Button,
  Modal,
  Form,
  Row,
  Col,
  Alert,   // vvv 1. เพิ่ม Alert ที่นี่ vvv
  Spinner, // vvv 2. เพิ่ม Spinner ที่นี่ vvv
} from "react-bootstrap";

// API URL (ID: 731472) และ Token ถูกต้อง
const BASE_URL = "https://api.baserow.io/api/database/rows/table/731472";
const API_TOKEN = "Token pJmbVWCV1NFQ7333Cmqj822hINsyguMW"; 

// --- ฟังก์ชันสำหรับแปลค่า Type ---
const getTypeId = (typeString) => {
  if (typeString === "นักศึกษา") {
    return 2;
  }
  return 1; 
};
const getTypeText = (typeId) => {
  if (typeId === 2) {
    return "นักศึกษา";
  }
  return "อาจารย์และบุคลากร";
};


export default function Products() {
  const { type } = useParams(); 
  
  const [events, setEvents] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  // State ของฟอร์ม (มี Register)
  const [event, setEvent] = useState({
    id: 0,
    Name: "",
    รายละเอียด: "",
    วันที่: "",
    product_image: "", 
    สังกัด: type, 
    Type: getTypeId(type),
    Register: "", 
  });

  const handleInputChange = (field, value) => {
    setEvent((prev) => ({ ...prev, [field]: value, }));
  };

  // ฟังก์ชัน list (กรองด้วย Type)
  function listEvents() {
    const typeId = getTypeId(type); 
    setLoading(true);
    let config = {
      method: "get",
      maxBodyLength: Infinity,
      url: `${BASE_URL}/?user_field_names=true&filter__Type__equal=${typeId}`,
      headers: { Authorization: API_TOKEN, },
    };
    axios.request(config)
      .then((response) => { setEvents(response.data.results); setLoading(false); setError(null); })
      .catch((error) => { console.log(error); setError("เกิดข้อผิดพลาดในการโหลดข้อมูล"); setLoading(false); });
  }
  
  // ฟังก์ชัน Edit
  const handleEdit = (obj) => {
    setEvent({
      id: obj.id,
      Name: obj.Name || "",
      รายละเอียด: obj.รายละเอียด || "",
      วันที่: obj.วันที่ || "",
      product_image: obj.product_image || "", 
      สังกัด: obj.สังกัด || type,
      Type: obj.Type || getTypeId(type),
      Register: obj.Register || "", 
    });
    setShow(true);
  };

  // ฟังก์ชัน Reset Form
  const resetEventForm = () => {
    const newType = type; 
    const newTypeId = getTypeId(newType); 
    setEvent({ id: 0, Name: "", รายละเอียด: "", วันที่: "", product_image: "", สังกัด: newType, Type: newTypeId, Register: "", });
  };

  // ฟังก์ชัน Create
  function createEvent() {
    if (event.Name.length <= 0) { alert("กรุณาใส่ชื่องาน"); return false; }
    let data = { Name: event.Name, รายละเอียด: event.รายละเอียด, วันที่: event.วันที่, product_image: event.product_image, สังกัด: event.สังกัด, Type: event.Type, Register: event.Register, };
    let config = { method: "post", maxBodyLength: Infinity, url: `${BASE_URL}/?user_field_names=true`, headers: { Authorization: API_TOKEN }, data: data, };
    axios.request(config)
      .then((response) => { setShow(false); listEvents(); resetEventForm(); setError(null); })
      .catch((error) => { console.log(error); setError("เกิดข้อผิดพลาดในการสร้างข้อมูล"); });
  }

  // ฟังก์ชัน Update
  function updateEvent(row_id) {
    let data = { Name: event.Name, รายละเอียด: event.รายละเอียด, วันที่: event.วันที่, product_image: event.product_image, สังกัด: event.สังกัด, Type: event.Type, Register: event.Register, };
    let config = { method: "patch", maxBodyLength: Infinity, url: `${BASE_URL}/${row_id}/?user_field_names=true`, headers: { Authorization: API_TOKEN }, data: data, };
    axios.request(config)
      .then((response) => { setShow(false); listEvents(); resetEventForm(); setError(null); })
      .catch((error) => { console.log(error); setError("เกิดข้อผิดพลาดในการอัปเดตข้อมูล"); });
  }

  // ฟังก์ชัน Delete
  function deleteEvent(row_id, name) {
    const confirmDelete = window.confirm(`คุณต้องการลบข้อมูลของ ${name} ใช่หรือไม่?`);
    if (!confirmDelete) return;
    let config = { method: "delete", maxBodyLength: Infinity, url: `${BASE_URL}/${row_id}/`, headers: { Authorization: API_TOKEN, }, };
    axios.request(config)
      .then((response) => { listEvents(); })
      .catch((error) => { console.log(error); setError("เกิดข้อผิดพลาดในการลบข้อมูล"); });
  }

  useEffect(() => { resetEventForm(); listEvents(); }, [type]); 

  // vvv 3. เพิ่มเงื่อนไข Loading vvv
  if (loading) {
    return <Spinner animation="border" role="status" className="mt-5" />;
  }
  
  // ส่วน Render (Card และ Modal)
  return (
    <Container className="p-4">
      <Row>
        <Col>
          <div align="left">
            <h1>รายการกิจกรรม: {type}</h1>
          </div>
        </Col>
        <Col>
          <div align="right">
            <Button
              variant="primary"
              onClick={() => [handleShow(), resetEventForm()]}
            >
              เพิ่มข้อมูล
            </Button>
          </div>
        </Col>
      </Row>

      {/* vvv 4. Alert ที่เคย Error จะทำงานได้แล้ว vvv */}
      {error && <Alert variant="danger">{error}</Alert>}
      
      <Row>
        {events.map((item) => (
          <Col sm={12} md={6} lg={3} key={item.id} className="mb-4">
            <Card>
              <Card.Img
                variant="top"
                src={item.product_image} 
                style={{ height: 250, objectFit: "cover" }}
              />
              <Card.Body>
                <Card.Title>{item.Name}</Card.Title>
                <Card.Text>
                  <div>วันที่: {item.วันที่}</div>
                </Card.Text>
                <Button variant="warning" onClick={(e) => handleEdit(item)}>
                  แก้ไข
                </Button>{" "}
                <Button
                  variant="danger"
                  onClick={(e) => deleteEvent(item.id, item.Name)}
                >
                  ลบ
                </Button>
                <Button 
                  variant="success" 
                  href={item.Register} 
                  target="_blank"      
                  rel="noopener noreferrer"
                  className="ms-1"     
                >
                  ลงทะเบียน
                </Button>
              </Card.Body>
            </Card>
          </Col>
        ))}
      </Row>

      {/* Modal Form */}
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>ฟอร์มข้อมูลกิจกรรม</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form.Group className="mb-3">
            <Form.Label>ชื่องาน</Form.Label>
            <Form.Control type="text" value={event.Name} onChange={(e) => handleInputChange("Name", e.target.value)} />
          </Form.Group>
          <Form.Group className="mb-3">
            <Form.Label>วันที่</Form.Label>
            <Form.Control type="text" value={event.วันที่} onChange={(e) => handleInputChange("วันที่", e.target.value)} />
          </Form.Group>
          <Form.Group className="mb-3">
            <Form.Label>รายละเอียด</Form.Label>
            <Form.Control as="textarea" rows={3} value={event.รายละเอียด} onChange={(e) => handleInputChange("รายละเอียด", e.target.value)} />
          </Form.Group>
          <Form.Group className="mb-3">
            <Form.Label>รูปกิจกรรม (URL)</Form.Label>
            <Form.Control type="text" value={event.product_image} onChange={(e) => handleInputChange("product_image", e.target.value)} />
          </Form.Group>
          <Form.Group className="mb-3">
            <Form.Label>ลิงก์ลงทะเบียน (URL)</Form.Label>
            <Form.Control type="text" value={event.Register} onChange={(e) => handleInputChange("Register", e.target.value)} />
          </Form.Group>
          <Form.Group className="mb-3">
            <Form.Label>สังกัด</Form.Label>
            <Form.Control type="text" value={event.สังกัด} onChange={(e) => handleInputChange("สังกัด", e.target.value)} readOnly />
          </Form.Group>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>ปิด</Button>
          <Button
            variant="primary"
            onClick={() => event.id > 0 ? updateEvent(event.id) : createEvent()}
          >
            บันทึก
          </Button>
        </Modal.Footer>
      </Modal>
    </Container>
  );
}